import React, { FC } from 'react'
import './Passenger.scss'
import PassengerInformation from '../../Components/PassengerInformation/PassengerInformation'
import SideBar from '../../Components/SideBar/SideBar'
import SideBarOrder from '../../Components/SideBarOrder/SideBarOrder'

const Passenger:FC = () => {
    return (
        <section className="passengerBlock content">
            <div className="container">
                <div className="passengerBlock-block content__top">
                    <PassengerInformation/>
                    <SideBar>
                        <SideBarOrder/>
                    </SideBar>
                </div>
            </div>
        </section>
    )
}

export default Passenger
