import React, { FC } from 'react'
import './PaymentMethod.scss'
import creditCard from '../../img/payment/creditCard.svg'
import google from '../../img/payment/google.svg'
import apple from '../../img/payment/apple.svg'
import paypal from '../../img/payment/paypal.svg'
import bitcoin from '../../img/payment/bitcoin.svg'
import { Link } from 'react-router-dom'
import googleBtn from '../../img/payment/googleBtn.svg'
import appleBtn from '../../img/payment/appleBtn.svg'
import facebookBtn from '../../img/payment/facebookBtn.svg'

const PaymentMethod:FC = () => {
    return (
        <section className="order payment">
            <div className="container">
                    <div className="order-block">
                        <h2 className="order__title">Payment method</h2>
                        <p className="order__subtitle">
                            Select a payment method below. Tripma processes
                            your payment securely with end-to-end encryption.
                        </p>
                        <ul className="payment__method">
                            <li className="payment__method-item">
                                <img src={creditCard} alt={creditCard} className="payment__method-img" />
                                <span className="payment__method-text">Credit card</span>
                                </li>
                            <li className="payment__method-item">
                                <img src={google} alt={google} className="payment__method-img" />
                                <span className="payment__method-text">Google Pay</span>
                            </li>
                            <li className="payment__method-item">
                                <img src={apple} alt={apple} className="payment__method-img" />
                                <span className="payment__method-text">Apple pay</span>
                            </li>
                            <li className="payment__method-item">
                                <img src={paypal} alt={paypal} className="payment__method-img" />
                                <span className="payment__method-text">Paypal</span>
                            </li>
                            <li className="payment__method-item">
                                <img src={bitcoin} alt={bitcoin} className="payment__method-img" />
                                <span className="payment__method-text">Crypto</span>
                            </li>
                        </ul>
                        <form action="#" className="form payment__form">
                            <div className="order__label payment__label">
                                <div  className="order__label-title">Credit card details</div>
                                <div className="order__checkbox-block">
                                    <input className="order__checkbox-item" type="checkbox" id="horns" name="horns"/>
                                    <label className="order__checkbox-text" htmlFor="horns">Billing address is same as Passenger 1 </label>
                                </div>
                                <input name="NameCard" type="text" className="form__input order__input payment__input" placeholder="Name on card" required/>
                                <input name="CardNumber"  type="number" className="form__input order__input payment__input" placeholder="Card number" required/>
                                <div className="payment__bottom">
                                     <div className="order__birth payment__bottom-birth">
                                        <input name="Expiration date" type="date" className="form__input payment__bottom-input" placeholder="Expiration date" required/>
                                        <span className="order__birth-date">MM/DD/YY</span>
                                    </div>
                                    <input name="CCV" type="number" className="form__input payment__bottom-input" placeholder="CCV" required/>
                                </div>
                            </div>
                            <div className="order__label">
                                <div  className="order__label-title">Create an account</div>
                                <p className="order__subtitle">
                                    Tripma is free to use as a guest, but if you create an account today, 
                                    you can save and view flights, manage your trips, earn rewards, and more.
                                </p>
                                <div className="order__checkbox-block">
                                    <input className="order__checkbox-item" type="checkbox" id="horns" name="horns"/>
                                    <label className="order__checkbox-text" htmlFor="horns">Same as Passenger 1</label>
                                </div>
                                <input name="Card number" type="text" className="form__input order__input payment__input" placeholder="Email address or phone number" required/>
                                <input name="NameCard" type="text" className="form__input order__input payment__input" placeholder="Password" required/>
                            </div>
                            <div className="modal__option payment__option">
                                <span>or</span>
                                </div>
                                <div className="modal__continue payment__continue">
                                    <Link to="#" className="modal__link form__btn">
                                        <img src={googleBtn} alt={googleBtn} className="modal__img" />
                                        <span className="modal__span">Continue with Google</span>
                                    </Link>
                                    <Link to="#" className="modal__link form__btn">
                                        <img src={appleBtn} alt={appleBtn} className="modal__img" />
                                        <span className="modal__span">Continue with Google</span>
                                    </Link>
                                    <Link to="#" className="modal__link form__btn">
                                        <img src={facebookBtn} alt={facebookBtn} className="modal__img" />
                                        <span className="modal__span">Continue with Google</span>
                                    </Link>
                            </div>
                            <div className="order__footer">
                                <h3 className="order__footer-title">Cancellation policy</h3>
                                <p className="order__footer-text">
                                    This flight has a flexible cancellation policy. 
                                    If you cancel or change your flight up to 30 days before the 
                                    departure date, you are eligible for a free refund. 
                                    All flights booked on Tripma are backed by our satisfaction guarantee, 
                                    however cancellation policies vary by airline. See the full cancellation policy for this flight.
                                </p>
                                <div className="order__footer-bottom">
                                    <Link to="/payment" className="order__footer-btn btn">Back to seat select</Link>
                                    <Link to="#" className="order__footer-btn btn">Confirm and pay</Link>
                                </div>
                            </div>
                        </form>
                    </div>
            </div>
        </section>
    )
}

export default PaymentMethod
