interface DefaultState {
    firstName: string,
    lastName: string,
    birth: string,
    email: string,
    phone: number, 
    travelNumber: number,
    checked: boolean 
}

enum PassengerActionTypes {
    firstNameHandler = "firstNameHandler",
    lastNameHandler = "lastNameHandler",
    birthHandler = "birthHandler",
    emailHandler = "emailHandler",
    phoneHandler = "phoneHandler",
    travelNumberHandler = "travelNumberHandler",
    checkedHandler = "checkedHandler"
}

interface FirstNameHandlerAction {
    type: PassengerActionTypes.firstNameHandler;
    payload: string
}

interface LastNameHandlerAction {
    type: PassengerActionTypes.lastNameHandler;
    payload: string
}

interface BirthHandlerAction {
    type: PassengerActionTypes.birthHandler;
    payload:  string
}

interface EmailHandlerAction {
    type: PassengerActionTypes.emailHandler;
    payload:  string
}

interface PhoneHandlerAction {
    type: PassengerActionTypes.phoneHandler;
    payload:  number
}

interface TravelNumberHandlerAction {
    type: PassengerActionTypes.travelNumberHandler;
    payload:  number
}

interface CheckedHandlerAction {
    type: PassengerActionTypes.checkedHandler;
    payload:  boolean
}

// 

const DefaultState: DefaultState = {
    firstName: '',
    lastName: '',
    birth: '',
    email: '',
    phone: 0, 
    travelNumber: 0,
    checked: false
}

type PassengerAction = FirstNameHandlerAction | LastNameHandlerAction | BirthHandlerAction | EmailHandlerAction | PhoneHandlerAction | TravelNumberHandlerAction | CheckedHandlerAction

export const passengerReducer = (state = DefaultState, action: PassengerAction):DefaultState => {
    switch (action.type) {
        case PassengerActionTypes.firstNameHandler:
            return {...state, firstName: action.payload}
        case PassengerActionTypes.lastNameHandler:
            return {...state, lastName: action.payload}
        case PassengerActionTypes.birthHandler:
            return {...state, birth: action.payload} 
        case PassengerActionTypes.emailHandler:
            return {...state, email: action.payload} 
        case PassengerActionTypes.phoneHandler:
            return {...state, phone: action.payload}   
        case PassengerActionTypes.travelNumberHandler:
            return {...state, travelNumber: action.payload}  
        case PassengerActionTypes.checkedHandler:
            return {...state, checked: action.payload}    
        default:
            return state
    }
}