import axios from 'axios'
import React, { FC, MouseEvent, useEffect, useState } from 'react'
import './Request.scss'

interface tariff {
    name: string,
    provider: string // "mts",
    url:  string,
    connection_price: number,
    price: number,
    pay_type: string, // "month",
    pay_currency: string // ("rub"),
    tv_services: [],
    speed: number,
    tv_channels: number
}

interface providers {
    url: string,
    name: string,
    alias: string
}

interface Api {
    tariffs: tariff[]
    providers: providers[]
    address: string
}

const Request:FC = () => {
    const [data, setData] = useState<Api>()
    const [loading, setLoading] = useState('')
    const [error, setError] = useState('')

    const getTariffs = async (e: MouseEvent<HTMLFormElement>) => {
        e.preventDefault();
        try {
            const API:any = axios.get(`http://212.47.227.248:12000/api/v1/tariffs?address=%D0%BC%D0%BE%D1%81%D0%BA%D0%B2%D0%B0,%20%20%D0%B0%D0%BC%D0%B1%D1%83%D0%BB%D0%B0%D1%82%D0%BE%D1%80%D0%BD%D1%8B%D0%B9%20%D0%BF%D1%80%D0%BE%D0%B5%D0%B7%D0%B4%2017`)

            const a:Api = (await API).data
            console.log(a);
            
            return setData(a);
        } catch (error) {
            console.log(error);
            
        }  
    }

    // useEffect(()=> {
    //     getTariffs()
    // }, [])

    return (
        <section className="request">
            <div className="container">
                <div className="request-block">
                    <form action="#" className="form request__form" onClick={getTariffs}>
                        <input className="form__input" type="text" />
                        <input className="form__input" type="submit" placeholder="Найти"/>
                    </form>
                    <table>
                        <tr>
                            <th>Тарифы</th>
                        </tr>
                        {data?.tariffs.map(el => {
                           return (
                            <tr>
                               <td>{el.name}</td>
                            </tr>
                            )
                        })}
                    </table>
                </div>
            </div>
        </section>
    )
}

export default Request
