import React, { FC, useState } from 'react'
import { Link } from 'react-router-dom'
import './PassengerInformation.scss'
import minus from '../../img/passenger/minus.svg'
import plus from '../../img/passenger/plus.svg'
import { useTypeSelector } from '../../Store'
import { useDispatch } from 'react-redux'

const PassengerInformation:FC = () => {

    const {firstName, lastName, birth, email, phone, travelNumber, checked} = useTypeSelector(state => state.passenger)
    const disptach = useDispatch()

    // Экстренные контакты
    const [firstNameContact, setFirstNameContact] = useState<string>('')
    const [lastNameContact, setLastNameContact] = useState<string>('')
    const [emailContact, setEmailContact] = useState<string>('')
    const [phoneContact, setPhoneContact] = useState<string>('')
    


    return (
        <section className="order">
            <div className="container">
                <div className="order-block">
                    <form action="#" className="form order__form">
                        <h2 className="order__title">Passenger information</h2>
                        <p className="order__subtitle">
                            Enter the required information for each traveler and be sure that it exactly matches the 
                            government-issued ID presented at the airport.
                        </p>
                        
                        <div className="order__label">
                            <h3 className="order__label-title">Passenger 1 (Adult)</h3>
                            <div className="order__label-block order__top">
                                <input value={firstName} onChange={(e) => disptach({type: "firstNameHandler", payload: e.target.value})} name="First name" type="text" className="form__input order__input" placeholder="First name*" required/>
                                <input name="Middle" type="text" className="form__input order__input"  placeholder="Middle"/>
                                <input value={lastName} onChange={(e) => disptach({type: "lastNameHandler", payload: e.target.value})} name="Last name" type="text" className="form__input order__input"  placeholder="Last name*" required/>
                                <input name="Suffix" type="text" className="form__input order__input"  placeholder="Suffix"/>
                                <div className="order__birth">
                                    <input value={birth} onChange={(e) => disptach({type: "birthHandler", payload: e.target.value})} name="Date of birth" type="text" className="form__input order__input"  placeholder="Date of birth*" required/>
                                    <span className="order__birth-date">MM/DD/YY</span>
                                </div>
                            </div>
                            <div className="order__label-block">
                                <input value={email} onChange={(e) => disptach({type: "emailHandler", payload: e.target.value})} name="Email address" type="email" className="form__input order__input" placeholder="Email address*" required/>
                                <input onChange={(e) => disptach({type: "phoneHandler", payload: e.target.value})} name="Phone number" type="tel" className="form__input order__input"  placeholder="Phone number*" required/>
                                <input name="Redress number" type="number" className="form__input order__input"  placeholder="Redress number"/>
                                <input onChange={(e) => disptach({type: "travelNumberHandler", payload: e.target.value})} name="Known traveller number" type="number" className="form__input order__input"  placeholder="Known traveller number*" required/>
                            </div>
                        </div>
                        <div className="order__label">
                            <h3 className="order__label-title">Emergency contact information</h3>
                            <div className="order__checkbox-block">
                                <input checked={checked} onChange={() => disptach({type: "checkedHandler", payload: !checked})} className="order__checkbox-item" type="checkbox" id="horns" name="horns"/>
                                <label className="order__checkbox-text" htmlFor="horns">Same as Passenger 1</label>
                            </div>
                            <div className="order__label-block">
                                <input type="checkbox" name="Same as Passenger 1" id="" />
                                <input value={checked ? firstName : firstNameContact} onChange={e => setFirstNameContact(e.target.value)} name="First name*" type="text" className="form__input order__input" placeholder="First name*" required/>
                                <input value={checked ? lastName : lastNameContact} onChange={e => setLastNameContact(e.target.value)} name="Last name*" type="text" className="form__input order__input"  placeholder="Last name*" required/>
                                <input value={checked ? email : emailContact} onChange={e => setEmailContact(e.target.value)} name="Email address*" type="email" className="form__input order__input" placeholder="Email address*" required/>
                                <input value={checked ? phone : phoneContact} onChange={e => setPhoneContact(e.target.value)} name="Phone number*" type="number" className="form__input order__input"  placeholder="Phone number*" required/>
                            </div>
                        </div>
                        <div className="order__footer">
                            <h3 className="order__footer-title">Bag information</h3>
                            <p className="order__footer-text">
                                Each passenger is allowed one free carry-on bag and one personal item. 
                                First checked bag for each passenger is also free. 
                                Second bag check fees are waived for loyalty program members. See the full bag policy.
                            </p>
                            <div className="order__bags">
                                <div className="order__bags-top">
                                    <span className="order__bags-title">Passenger 1</span>
                                    <span className="order__bags-title">Checked bags</span>
                                </div>
                                <div className="order__bags-bottom">
                                    <span className="order__bags-title">First Last</span>
                                    <div className="order__bags-counter">
                                        <img src={minus} alt={minus} className="order__bags-img" />
                                        <span className="order__bags-number">1</span>
                                        <img src={plus} alt={plus} className="order__bags-img" />
                                    </div>
                                </div>
                            </div>
                            <div className="order__footer-bottom">
                                <Link to="/payment" className="order__footer-btn btn">Save and close</Link>
                                <Link to="#" className="order__footer-btn btn">Select seats</Link>
                            </div>
                        </div>
                    </form>
                </div>
            </div>
        </section>
    )
}

export default PassengerInformation
