import React, { FC } from 'react'
import { Link } from 'react-router-dom'
import './SideBarOrder.scss'
import img from '../../img/sidebar/img.svg'
import Hawai from '../../img/sidebar/Hawai.svg'

const SideBarOrder:FC = () => {
    return (
        <div className="sidebar__order">
            <table className="sidebar__table">
                <tbody className="sidebar__table-body">
                    <tr className="sidebar__table-string">
                        <td className="sidebar__table-item">
                            <img src={Hawai} alt={Hawai} />
                            <h4 className="sidebar__table-title">Hawaiian Airlines</h4>
                            <span className="sidebar__table-subtitle">FIG4312</span>
                        </td>
                        <td className="sidebar__table-item">
                            <span className="sidebar__table-title">16h 45m (+1d)</span>
                            <span className="sidebar__table-title">7:00 AM - 4:15 PM</span>
                            <span className="sidebar__table-subtitle">2h 45m in HNL</span>
                        </td>
                    </tr>
                    <tr className="sidebar__table-string">
                        <td className="sidebar__table-item">
                            <img src={Hawai} alt={Hawai} />
                            <h4 className="sidebar__table-title">Hawaiian Airlines</h4>
                            <span className="sidebar__table-subtitle">FIG4312</span>
                        </td>
                        <td className="sidebar__table-item">
                            <span className="sidebar__table-title">16h 45m (+1d)</span>
                            <span className="sidebar__table-title">7:00 AM - 4:15 PM</span>
                            <span className="sidebar__table-subtitle">2h 45m in HNL</span>
                        </td>
                    </tr>
                </tbody>
            </table>
            <div className="sidebar__total">
                <div className="sidebar__total-item">
                    <span className="sidebar__total-text">Subtotal</span>
                    <span className="sidebar__total-text">$503</span>
                </div>
                <div className="sidebar__total-item">
                    <span className="sidebar__total-text">Taxes and Fees</span>
                    <span className="sidebar__total-text">$121</span>
                </div>
                <div className="sidebar__total-item">
                    <span className="sidebar__total-text">Total</span>
                    <span className="sidebar__total-text">$624</span>
                </div>
            </div>
            <Link className="sidebar__btn btn" to="/payment">Select seats</Link>
            <img className="sidebar__img" src={img} alt={img} />
        </div>
    )
}

export default SideBarOrder
